import React, { useState } from "react";
import "./Onglets.css";
import "./Slide.css";

import SlideshowManchettes from "./SlideManchettes";
import SlideshowLyto from "./SlideLyto";
import SlideshowBraceletsW from "./SlideBraceletsW";
import SlideshowBraceletsFins from "./SlideBraceletsFins";

import productsLyto from "./products/ProductsLyto";
import productsManchettes from "./products/ProductsManchettes";
import productsBraceletsW from "./products/ProductsBraceletsW";
import productsBraceletsFins from "./products/ProductsBraceletsFins";

export default function Onglets() {
  const [stateOnglets, setStateOnglets] = useState(1);

  const go1 = () => {
    setStateOnglets(1);
  };

  const go2 = () => {
    setStateOnglets(2);
  };

  const go3 = () => {
    setStateOnglets(3);
  };

  const go4 = () => {
    setStateOnglets(4);
  };

  return (
    <div className="contain">
      <div className="contBtn">
        <div
          onClick={go1}
          className={`onglets ${stateOnglets === 1 ? "active" : ""}`}
        >
          {" "}
          <h1>Manchettes arbre de vie.</h1>
        </div>
        <div
          onClick={go2}
          className={`onglets ${stateOnglets === 2 ? "active" : ""}`}
        >
          <h1>Bracelets arbre de vie lytothérapie..</h1>
        </div>
        <div
          onClick={go3}
          className={`onglets ${stateOnglets === 3 ? "active" : ""}`}
        >
          <h1>Bracelets en fil d'aluminium</h1>
        </div>
        <div
          onClick={go4}
          className={`onglets ${stateOnglets === 4 ? "active" : ""}`}
        >
          <h1>Bracelets fins.</h1>
        </div>
      </div>
      <div className="container">
        {stateOnglets === 1 ? (
          <div className="cont">
            <section className="slideshowManchettes">
              {productsManchettes.map(product => (
                <SlideshowManchettes
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) : stateOnglets === 2 ? (
          <div className="cont">
            <section className="slideshowLyto">
              {productsLyto.map(product => (
                <SlideshowLyto
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) : stateOnglets === 3 ? (
          <div className="cont">
            <section className="slideshowBraceletsW">
              {productsBraceletsW.map(product => (
                <SlideshowBraceletsW
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        
        )
        : (
          <div className="cont">
            <section className="slideshowBraceletsFins">
              {productsBraceletsFins.map(product => (
                <SlideshowBraceletsFins
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        )
      }
      </div>
    </div>
  );
}
